/**
* ------------------------------------------------------------------------
* Base API exports
* ------------------------------------------------------------------------
*/

import semver from 'semver';
import cli from './lib/cli';
import log from './lib/log';
import ui from './lib/ui';
import sh from './lib/sh';
import utils from './lib/utils';
import error from './lib/error';

// Since commander doesn't support yet Global options
// we'll be using ENV vars like COLOR and LOG_LEVEL
// Check more here https://github.com/tj/commander.js/issues/476
// cli.option('-C, --no-color [color]', 'disable colors terminal output.');
// cli.option('-l, --log-level [logLevel]', 'set log level.');
const COLOR = JSON.parse(process.env.COLOR || true);
const LOG_LEVEL = process.env.LOG_LEVEL || 'info';

// set color support
if (!COLOR) {
  // disable logger colors
  log.disableColor();
  // colors
  ui.colors.enabled = false;
}
// Set the log level
log.level = LOG_LEVEL;

export {
  cli,
  log,
  ui,
  sh,
  utils,
  error,
  semver,
};
